import './main.scss'

window.game = {
    blocks: {},
    renderBlock: function (blockName, container) {
        this.blocks[blockName](container)
    },
    card: [],
    arrayFront: [],
    arrayShirt: [],
    cardShirt: null,
    cardFront: null,
}

const body = document.querySelector('body')
const buttonStart = document.querySelector('.game__start')
const gameLevelButton1 = document.querySelector('.game__level_button1')
const gameLevelButton2 = document.querySelector('.game__level_button2')
const gameLevelButton3 = document.querySelector('.game__level_button3')
// const fieldCard = document.querySelector('.field__card')
const menu = document.querySelector('.menu')
body.appendChild(menu)
const gameCard = document.createElement('div')
gameCard.classList.add('game__card')
const gameProcess = document.querySelector('.gameprocess')
let array = []
const arrCard = [
    'static/1.png',
    'static/2.png',
    'static/3.png',
    'static/4.png',
    'static/5.png',
    'static/6.png',
    'static/7.png',
    'static/8.png',
    'static/9.png',
    'static/10.png',
    'static/11.png',
    'static/12.png',
    'static/13.png',
    'static/14.png',
    'static/15.png',
    'static/16.png',
    'static/17.png',
    'static/18.png',
    'static/19.png',
    'static/20.png',
    'static/21.png',
    'static/22.png',
    'static/23.png',
    'static/24.png',
    'static/25.png',
    'static/26.png',
    'static/27.png',
    'static/28.png',
    'static/29.png',
    'static/30.png',
    'static/31.png',
    'static/32.png',
    'static/33.png',
    'static/34.png',
    'static/35.png',
    'static/36.png',
]

gameLevelButton1.addEventListener('click', () =>
    addFocus(gameLevelButton1, gameLevelButton2, gameLevelButton3)
)
gameLevelButton2.addEventListener('click', () =>
    addFocus(gameLevelButton2, gameLevelButton3, gameLevelButton1)
)
gameLevelButton3.addEventListener('click', () =>
    addFocus(gameLevelButton3, gameLevelButton2, gameLevelButton1)
)

function addFocus(element1, element2, element3) {
    element1.classList.add('focus')
    element2.classList.remove('focus')
    element3.classList.remove('focus')
}

buttonStart.addEventListener('click', getLevelDifficulty)

function getLevelDifficulty() {
    getField()
    if (gameLevelButton1.classList.contains('focus')) {
        getOpenCard(3)
    } else if (gameLevelButton2.classList.contains('focus')) {
        getOpenCard(6)
    } else if (gameLevelButton3.classList.contains('focus')) {
        getOpenCard(9)
    }
}

window.game.blocks['level'] = getLevelDifficulty

function getField() {
    body.innerHTML = ''
    gameProcess.classList.remove('hidden')
    body.appendChild(gameProcess)
}

function getOpenCard(a) {
    for (let i = 0; i < a; i++) {
        const random = Math.floor(Math.random() * Object.values(arrCard).length)
        array.push(Object.values(arrCard)[random])
    }

    array = array.concat(array)
    array.sort(() => Math.random() - 0.5)
    shuffle(array)
    const arrayFront = []
    const arrayShirt = []

    array.forEach((i) => {
        const card = document.createElement('div')

        const cardFront = document.createElement('img')
        cardFront.setAttribute('src', i)
        cardFront.classList.add('card')
        body.appendChild(gameCard)
        gameCard.appendChild(card)
        card.appendChild(cardFront)
        arrayFront.push(cardFront)

        const cardShirt = document.createElement('img')
        cardShirt.src = 'static/shirt.png'
        cardShirt.classList.add('hidden')
        body.appendChild(gameCard)
        gameCard.appendChild(card)
        card.appendChild(cardShirt)
        arrayShirt.push(cardShirt)

        card.addEventListener('click', () => {
            cardFront.classList.remove('hidden')
            cardShirt.classList.add('hidden')
        })

        window.game.card = card
        window.game.arrayShirt = arrayShirt
        window.game.cardShirt = cardShirt
        window.game.arrayFront = arrayFront
        window.game.cardFront = cardFront
        setTimeout(getFieldCard, 5000)
    })
}

function getFieldCard() {
    for (let i = 0; i < window.game.arrayFront.length; i++) {
        window.game.arrayFront[i].classList.add('hidden')
    }
    for (let i = 0; i < window.game.arrayShirt.length; i++) {
        window.game.arrayShirt[i].classList.remove('hidden')
    }
}

function shuffle(array) {
    for (let i = array.length - 1; i > 0; i--) {
        let j = Math.floor(Math.random() * (i + 1))
        ;[array[i], array[j]] = [array[j], array[i]]
    }
}
